@import '../../styles/utils/mixins';

#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.main-container {
  height: 100%;
  display: grid;
  grid-template-columns: 130px 1fr;

  @include OnTablet {
    grid-template-columns: 200px 1fr;
  }
}

.main {
  height: 100%;
  position: relative;
}
